						/* check for errors and display them
						console.dir(
							response.result.call_params.person_rodne_cislo,
							response.result.call_params.auto_vin,
							response.result.validform,
							response.result.form_validation_error_elements,
							response.result.form_validation_error_tab_elements,
						);*/
						// console.dir(response.result);
						this.loading = false;
						if (response.result.validform === true) {
							STATE_modals().set_modal_event_data('specify_recalculate');
							STATE_modals().close_them_all();
						} else {
							console.log(response.result?.form_validation_error_elements);
							this.price_errors = response.result.form_validation_error_elements;
						}
