					},
					auto_vin: {
						label: 'VIN',
						placeholder: 'VIN číslo vozidla',
					},
				};
				for (let key in wording) {
					if (Functions.ends_with(token, key)) {
						return wording[key][type];
					}
				}
				return token;
			},*/
		},
