<template>
	<div
		:class="is_disabled ? 'the_form_is_disabled' : ''"
		class="row g-0 global_form"
		data-cy="global_form"
	>

		<!-- loop -->
		<div
			v-for="(field_data, field_id) in global_inputs"
			class="col-sm-4"
		>

			<!-- label, checkbox has its own label -->
			<label
				v-if="field_data.type !== 'checkbox'"
				:for="field_id"
			>
				{{ field_data.label }}
			</label>

			<!-- input -->
			<Checkbox
				v-if="field_data.type === 'checkbox'"
				:field_id="field_id"
				:field_data="field_data"
				emit_name="validate_global_changed"
				@validate_global_changed="$emit('validate_global_changed')"
			/>
			<Select
				v-else-if="field_data.type === 'select'"
				:field_id="field_id"
				:field_data="field_data"
				emit_name="validate_global_changed"
				@validate_global_changed="$emit('validate_global_changed')"
			/>
			<Text
				v-else-if="field_data.type === 'text'"
				:field_id="field_id"
				:field_data="field_data"
				emit_name="validate_global_changed"
				@validate_global_changed="$emit('validate_global_changed')"
			/>
			<Datepicker
				v-else-if="field_data.type === 'date'"
				:field_id="field_id"
				:field_data="field_data"
				emit_name="validate_global_changed"
				@validate_global_changed="$emit('validate_global_changed')"
			/>
			<Slider
				v-else-if="field_data.type === 'slider'"
				:field_id="field_id"
				:field_data="field_data"
				emit_name="validate_global_changed"
				@validate_global_changed="$emit('validate_global_changed')"
			/>

			<!-- error -->
			<div
				v-for="(message, error_id) in global_errors[field_id]"
				class="validation-placeholder validation-message"
			>
				<span class="mark-circle mark-orange">!</span>
				{{ message }}
			</div>

		</div>
		<!-- END loop -->

		<!-- specify price modal button -->
		<div
			v-if="price_inputs"
			@click="open_price"
			class="offset-4 col-sm-4"
		>
			<button
				:class="price_has_error ? 'btn-accent' : 'btn-white'"
				class="btn w-100"
			>
				{{ price_has_error ? 'Opraviť hodnoty' : 'Upresniť cenu' }}
			</button>
		</div>

	</div>
</template>

<script>
	import Checkbox from '@/components/forms/basic_inputs/Checkbox.vue';
	import Select from '@/components/forms/basic_inputs/Select.vue';
	import Text from '@/components/forms/basic_inputs/Text.vue';
	import Datepicker from '@/components/forms/advanced_inputs/Datepicker.vue';
	import Slider from '@/components/forms/advanced_inputs/Slider.vue';
	import STATE_modals from '@/store/STATE_modals.js';

	export default {
		props: {
			global_disabled: Boolean,
			global_inputs: Object,
			price_inputs: Object,
			global_errors: Object,
		},
		components: {
			Checkbox,
			Select,
			Text,
			Datepicker,
			Slider,
		},
		emits: ['validate_global_changed'],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			open_price()
			{
				// store data
				STATE_modals().store_modal_data(
					{
						launched_from: 'from_global',
						product_id: null,
						product_name: null,
						price_inputs: this.price_inputs,
						price_errors: this.global_errors,
					}
				);

				// open modal
				STATE_modals().open_it('Specify_product_price');
			},
		},
		computed: {
			is_disabled()
			{
				return this.global_disabled;
			},
			price_has_error()
			{
				let errors = Object.keys(this.global_errors);
				let inputs = Object.keys(this.price_inputs);
				for (let index = 0; index < errors.length; index++) {
					if (inputs.indexOf(errors[index]) > -1) {
						return true;
					}
				}
				return false;
			},
		},
	}
</script>

<style scoped lang="scss">
	.the_form_is_disabled {
		pointer-events: none;
		opacity: 0.5;
	}
</style>
